import {React, useEffect, useState, useRef} from 'react';
import { useNavigate, useLocation } from 'react-router-dom';

import  { parseISO} from 'date-fns';

import API from '../../../../Services';
import PageWrapper from '../../../../layout/PageWrapper/PageWrapper';
import Page from '../../../../layout/Page/Page';
import 	Button, {ButtonGroup} from '../../../../components/bootstrap/Button';
import Modal, {ModalBody,ModalFooter,ModalHeader,ModalTitle} from '../../../../components/bootstrap/Modal';
import { cirurgicalFlow } from '../../../../menu';
import Header from '../../../../components/Header';
import  {DropdownItem, DropdownMenu, DropdownToggle} from '../../../../components/bootstrap/Dropdown';
import Dropdown from '../../../../components/bootstrap/Dropdown';
import "./style.css"


const Listagem = () => {

const navigate = useNavigate()
const location = useLocation()



/**
 * Variáveis de Estado
 */

const [patients, 			setPatients] 		= useState(	[]	)
const [Loading, 			setLoading] 		= useState(true)
const [CodigoLocalidade, 	setCodigoLocalidade		] 		= useState(null	)
const [CodigoSubLocalidade, setCodigoSubLocalidade	] 		= useState(null	)
const [time,            	setTime					] 		= useState(Date.now())

/**
 * Controlador do Botão
 */

const [buttonStatusAptos, 	setButtonStatusAptos	] 		= useState(false	)
const [buttonStatusTodos, 	setButtonStatusTodos	] 		= useState(false)
const [DisableButtonAptos, 	setDisableButtonAptos	] 		= useState(false)
const [DisableButtonTodos, 	setDisableButtonTodos	] 		= useState(false)

const desabilitarSelecao = useRef(null)


// eslint-disable-next-line no-unused-vars
const [ModalCirurgiaCancelada, 	setModalCirurgiaCancelada] 	= useState(false)




const [mostraBotao, 	setMostraBotao	] 		= useState(false)


useEffect(() => {
	
	
	/**
	 * 
	 * Ao carregar a página, a primeira verificação é se existe algum paciente no quadro informativo, se sim, o usuário será redirecionado.
	 */
	 const checaStatusQuadro = async(codigoSubLocalidade) => 
	 {
		 /**
		  * Realizará nova consulta para recuperar dados da próxima etapa.
		  */
		 try {
			 const Consulta = await API.get(`/verificaExistenciaQuadroRPA/${codigoSubLocalidade}`)
			 if(Consulta)
			 {
				 if(Consulta.data.length)
				 {
					 const session = JSON.parse(localStorage.getItem('CAPP-local'))
 
					 const initialParsedDateBed = parseISO(Consulta.data[0].ENTRADA);
					 
					 
					 navigate('/rpa/quadroInfo', {
							 state: {
								 PULOU_ETAPA: true,
								 CODIGO_LOCALIDADE: session.CODIGO_LOCALIDADE,
								 CODIGO_SUB_LOCALIDADE: session.CODIGO_SUBLOCALIDADE,
								 DADOS: Consulta.data[0],
								 DATA_INICIO_JEJUM: Consulta.data[0].INICIO_JEJUM,
								 DATA_ENTRADA: initialParsedDateBed
							 }
					 
						 });
	 
					 
					 
				 }
		 }
			 
		 } catch (error) {
			 console.log(error)
		 }
 };

 	
 	checaStatusQuadro		(location.state.CODIGO_SUBLOCALIDADE)
/*
	ListarPacientes			(2,location.state.CODIGO_LOCALIDADE	)
*/
	setCodigoLocalidade		(location.state.CODIGO_LOCALIDADE	)
	setCodigoSubLocalidade	(location.state.CODIGO_SUBLOCALIDADE)
	setMostraBotao(true)


const interval = setInterval(() => {
	setTime(Date.now())
	setMostraBotao(false)
}, 30000);
		



return () => {
	
	
	ListarPacientes(2,location.state.CODIGO_LOCALIDADE	)
	//console.log(`código da localidade: ${location.state.CODIGO_LOCALIDADE} `)
	clearInterval(interval);

};


}, [location, navigate, CodigoSubLocalidade, time,  desabilitarSelecao]);


const refreshFiltro = (param) => {

	if(param==='todos')
	{

		setPatients([])
		ListarPacientes(1, CodigoLocalidade)

		setDisableButtonAptos(true)		
		setButtonStatusAptos(false)
		
	}
	
	if(param==='aptos')
	{
		setPatients([])
		ListarPacientes(2, CodigoLocalidade)
		
		setDisableButtonAptos(false)
		setDisableButtonTodos(true)	
				
	}
}



const ListarPacientes = async (paramStatus, paramCodigoLocalidade) => {
	setMostraBotao(true)
	if(paramStatus === 1)
	{
		try {	
			setLoading(true)		
			const patient = await API.get(`/retornaCirurgiasAgendadasByLocalidade?cd_localidade=${paramCodigoLocalidade}`);
			setPatients(patient.data)
			setDisableButtonAptos(false)
			setButtonStatusAptos(false)
			setButtonStatusTodos(true)
		} catch (error) {
			console.log(error)
		}
		setLoading(false)
	}
	else if(paramStatus === 2)
	{

		try {		
			setLoading(true)	
			const patient = await API.get(`/retornaCirurgiasAgendadasByLocalidade?cd_localidade=${paramCodigoLocalidade}&status=apto`);
			setPatients(patient.data)
			setButtonStatusAptos	(true)
			setButtonStatusTodos	(false)
			setDisableButtonTodos	(false)
		} catch (error) {
			console.log(error)
		}
		setLoading(false)
	}
	
};



const selecionarPaciente = async(infoPaciente) => {
	/**
	 * Realizará nova consulta para recuperar dados da próxima etapa.
	 */
	try {
		const RecuperaDadosProximaEtapa = await API.get(`/retornaProximaEtapa?cd_localidade=${CodigoLocalidade}&cod_aviso=${infoPaciente.COD_AVISO}`)
		if(RecuperaDadosProximaEtapa)
		{
			navigate('/rpa/assinaturaRecebimento', {
				state: {
					CODIGO_LOCALIDADE: CodigoLocalidade,
					CODIGO_SUB_LOCALIDADE: CodigoSubLocalidade,
					DADOS: infoPaciente,
					INFO_PROX_ETAPA: {
						PROX_ETAPA: {
							PROX_ETAPA: 123
						}
					}
				}
		
			  });
		}
	} catch (error) {
		console.log(error)
	}

	
};



/*
* Caso seja clicado no MENU na barra superior, irá quebrar o LocalStorage e retornará a página inicial.
*/

const alterarLocalidade = () => {

	localStorage.removeItem('CAPP-local');
	navigate('/')

};

const session = JSON.parse(localStorage.getItem('CAPP-local'))
const localidadeHeader = `SRPA L${session.SUBLOCALIDADE.split(' ')[1]}`;

	return (
		
		<PageWrapper title={cirurgicalFlow.RPA_LISTAGEM.text}>
		
			<Header titulo={localidadeHeader} localidade='Lista de Pacientes'>
				<Dropdown >
					<DropdownToggle>
						<Button
							color="light"
							icon="menu"
							isLight
						>
							<p> </p>
						</Button>
					</DropdownToggle>
					<DropdownMenu>
						<DropdownItem onClick={() => alterarLocalidade()}>
							Alterar Local
						</DropdownItem>
					</DropdownMenu>
				</Dropdown>
			</Header>
			<Page>
			
			<div className='position-filter'>
					{
						mostraBotao  &&
						<>
							<Button size='lg'  rounded={3} disabled={DisableButtonAptos} color="info" isActive={buttonStatusAptos} isLight onClick={()=> refreshFiltro('aptos')}>
								Aptos
							</Button>

							<Button size='lg' id="btnTodos"  rounded={3} disabled={DisableButtonTodos} style={{"margin": "3px"}} color="info" isActive={buttonStatusTodos}  isLight onClick={()=> refreshFiltro('todos')}>
								Todos
							</Button>
						</>
					}
				
				
				</div>
				<br />
			
				<div>
					
				{
					// eslint-disable-next-line no-nested-ternary
					Loading  ?
						<div className="lds-ring"><div /><div /><div /><div /></div>
					:
					// eslint-disable-next-line no-nested-ternary
					patients.length ?	
						patients.map((i) => {
							return (
								// eslint-disable-next-line jsx-a11y/no-static-element-interactions
								<div key={i.CD_AVISO} className={ i.CANCELADO == null ? 'bloco-paciente' : 'bloco-paciente-cancelado' }  onClick={ i.CANCELADO == null ? ()=>selecionarPaciente(i) : ()=>setModalCirurgiaCancelada(true)} onKeyDown={ i.CANCELADO == null ? ()=>selecionarPaciente(i) : ()=>setModalCirurgiaCancelada(true)}>
								<div className='fw-bold'>
										{i.PACIENTE}
									</div>											
									<div className='text-muted'>
										<small style={{"color": i.CANCELADO != null && `#fff`}}>{i.NM_MAE}</small>
									</div>
									<div className='data_nasc'>
										{i.DT_NASCIMENTO}
										{ i.CANCELADO != null && <input className="btn-info-susp-cir" type="button" value="SUSPENSA"/>}
										
									</div>
								</div>
								
							
							);
						})
						:
						<div className="d-flex justify-content-center">
							<br /><br /><br /><br />
							<h1>Não há paciente!</h1>
						</div>

              		 
               }
			   </div>

			   
			   <Modal
					id='modalCancelamentoCirurgia'
					isOpen={ModalCirurgiaCancelada}
					titleId='modalCancelamentoCirurgia'>
						<ModalHeader style={{"backgroundColor":"#E5684C"}}>
							<ModalTitle style={{"color": "#fff"}} id='example-title'> 
								ALERTA
							</ModalTitle>
						</ModalHeader>
						<ModalBody className='ModalBody'>
							<h1>Esta cirurgia foi suspensa!</h1>
						</ModalBody>
						<ModalFooter>
							<ButtonGroup>
									<Button onClick={()=> setModalCirurgiaCancelada(false)} className='btn-style' color='light'>
										Fechar
									</Button>
							</ButtonGroup>
						</ModalFooter>
				</Modal>	


			</Page>
		</PageWrapper>
	);

	
};

export default Listagem;
